.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #1c1a1a;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 1px; /* ลดระยะห่างระหว่างโลโก้และ Header */
}

.header__search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
  background-color: #fff; /* เปลี่ยนสีพื้นหลังของช่องค้นหาเป็นสีขาว */
}

.header__searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%
}

.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #a9465b;
}

.header__optionLineOne {
  font-size: 10px;
}

.header__optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}

.header__optionBasket {
  display: flex;
  align-items: center;
  color: #fff; 
}

.header__basketCount {
  margin-left: 10px;
  margin-right: 10px;
}

.header__nav {
  display: flex;
  justify-content: space-evenly;
}

.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: #fff; /* เปลี่ยนสีข้อความให้เป็นสีขาว */
}